import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const Header = resolve => require.ensure([], () => resolve(require('@/components/Header')), 'Header');
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Header',
      component: Header
    }
  ]
})



