<style lang="stylus">
.header-area
    width:100%
    height:170px
    margin: 0 auto
    background: #fff;
.header-area-search
    width:1200px
    height:128px
    margin: 0 auto
.header-area-nav
    width:1200px
    height:42px
    margin: 0 auto
    background: #3089dc
.header-nav-ul
    height:100%
.header-nav-ul li
    float: left
    line-height: 42px
    padding: 0 25px
    display: block
    cursor:pointer
    &:hover
        background: rgb(17, 112, 188);
.header-nav-ul li a{
    color:#fff
    display: block;
}
</style>

<template>
    <div class="js-header">
        <div class="header-area">
            <div class="header-area-search"></div>
            <div class="header-area-nav">
                <ul class="header-nav-ul">
                    <li>
                        <a>首页</a>
                    </li>
                    <li>
                        <a>国际站模板</a>
                    </li>
                    <li>
                        <a>中文站模板</a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Header',
    data() {
        return {

        }
    }
}
</script>



