<style lang="stylus">
.js-menu
    float: left
    width: 220px
    color: #4c4c4c
    background: #fff
    padding: 0 10px
	.menu-option
		padding: 20px 0 5px 0
		border-bottom:1px solid #f2f2f5
	.menu-option-tit
		font-weight: bold
	.menu-option-ul
		margin:15px 0 0 0
		width: 208px
	.menu-option-ul li a
		font-size: 12px
	.menu-option-ul li
		width: 47%
		float:left
		padding: 5px 15px
		margin: 0 3% 15px 0
		text-align: center
		background: #f4f4f4
		cursor: pointer
	.menu-option-ul li.active 
		background: #3089dc
		a
			color: #fff
	.menu-option-color
		width: 210px;
		margin: 15px 0 0 0
		li
			width:30px
			height:22px
			overflow: hidden
			float: left
			border-radius: 4px
			margin: 0 12px 15px 0
			cursor:pointer
			text-align: center
			position:relative
		.selectColor 
			display:none
			position:absolute
			top: 2px
			left:8px
		.active .selectColor
			display:block
			font-size: 16px
			color:#fff
		.red
			background: #ff0000
		.whiteBlack
			background: url(../../static/images/whiteBlack.png) no-repeat
			background-size: 100% 100%
		.black
			background: #000
		.greed
			background: #00ff00
		.blue
			background: #0000ff
		.pink
			background: #ffc0cb
		.brown
			background: #A67D3D
		.gray
			background: #808080
		.color
			background: url(../../static/images/color.png) no-repeat
			background-size: 100% 100%
		.purple
			background: #808080
		.orange
			background: #ffa500
		.yellow
			background: #f1f12b;
</style>

<template>
	<div class="js-menu">
		<div class="menu-option">
			<h3 class="menu-option-tit">模板行业</h3>
			<ul class="fix menu-option-ul">
				<li v-for="item in menuList" :key="item" :class="[{active:activeName==item}]" @click="selected(item)">
					<a>{{item}}</a>
				</li>
			</ul>
		</div>
		<div class="menu-option">
			<h3 class="menu-option-tit">色系分类</h3>
			<ul class="fix menu-option-color">
				<li v-for="item in colorList" :key="item" :class="[item,{active:activeColor==item}]" @click="selectCorlor(item)">
					<i class="fa fa-check selectColor" aria-hidden="true"></i>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
export default {
	name: 'Header',
	data() {
		return {
			activeName: '',
			activeColor: '',
			menuList: ['女装', '鞋类', '五金机械', '工艺礼品'],
			colorList: ['red', 'black', 'whiteBlack', 'greed', 'blue', 'pink', 'brown', 'gray', 'color', 'purple', 'orange', 'yellow']
		}
	},
	methods: {
		selected(val) {
			this.activeName = val;
		},
		selectCorlor(val) {
			this.activeColor = val;
		}
	}
}
</script>



